
$(document).ready(function(){
	$('#open_mobile_menu').on('click', function(e){
		e.preventDefault();
		$('#mobile-menu').addClass('active');
		bodyLock();
		showBackFrame();
	});
	$('.back_frame').on('click', function(e){
		e.preventDefault();
		closeBackFrame();
	});

	if ($('.carousel').length){
		$('.carousel').carousel({
			interval: 3000,
			ride:true,
			pause: false
		});
	}
	$('.btn-close').on('click', function(){
		closeBackFrame();
	})
	
	$('.minus').click(function () {
		var $input = $(this).parent().find('input');
		var count = parseInt($input.val()) - 1;
		count = count < 0 ? 0 : count;
		$input.val(count);
		$input.change();
		return false;
	 });
	 $('.plus').click(function () {
		var $input = $(this).parent().find('input');
		$input.val(parseInt($input.val()) + 1);
		$input.change();
		return false;
	 });

	 Fancybox.bind('[data-fancybox="gallery"]', {
		hideScrollbar: false,
	 });


	 $('.left-menu__item').on('click', function(e){
		e.preventDefault();
		if($(this).hasClass('active')){
			$(this).removeClass('active');
			return;
		}
		$('.left-menu__item').removeClass('active');
		$(this).addClass("active");
	 })
})

function showBackFrame(){
	$('.back_frame').addClass('active');
}
function closeBackFrame(){
	$('.back_frame').removeClass('active');
	$('#mobile-menu').removeClass('active');
	$('body').removeClass('lock');
}
function bodyLock(){
	$('body').addClass('lock');
}